@media print {
  .HideInPrint {
    display: none;
  }

  .PreviewResume {
    display: block;
  }  
}

@media print {
  body *,
  *::before,
  *::after {
    -webkit-print-color-adjust: exact !important; /* Chrome, Safari, Edge */
    print-color-adjust: exact !important; /*Firefox*/
  }
}

@page {
  size: A4 !important;
  margin: 0.3cm !important;
  resolution: 300dpi;    
  border: none;
}

.Preview {
  display: flex;
  /* use flexbox layout */
  justify-content: center;
  /* center horizontally */
  align-items: center;
  /* center vertically */
}

.PreviewResume {
  display: flex;
  /* use flexbox layout */
  justify-content: center;
  /* center horizontally */
}