.LegendBox {
    border: 1px solid #81e46d;
    border-Radius: 5px;
}

.LegendTextBig {
    color: blue;
    font-size: large;
    text-align: left;
}

.LegendTextSmall {
    color: blue;
    font-size: small;
    text-align: left;
}


.SmallText {
    text-align: left;
    padding: 0px auto;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 0.75rem;
    line-height: 1rem;
    font-weight: 400;
    letter-spacing: 0.00938em;
    font-family: Roboto, Helvetica, Arial, sans-serif;
}

.MuiStepConnector-lineVertical {
    max-height: 5px !important;
    min-height: 5px !important;
}

.MediumText {
    text-align: left;
    padding: 0px auto;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 16px;
    font-weight: bold;
    line-height: 1rem;
    letter-spacing: 0.00938em;
    font-family: Roboto, Helvetica, Arial, sans-serif;
}

.Chips {
    border-Radius: 5px !important;
    border-Color: rgb(129, 228, 109) !important;
}

.UlList {
    text-Align: left;
    margin: 0px;
    padding-Left: 15px;
    list-style: circle;
}